#eicmslinks_body {
  font: 400 14px/1.42857 Open Sans, Helvetica, Arial, sans-serif; }

#eicmslinks_body,
#eicmslinks_body *,
#eicmslinks_body *:after,
#eicmslinks_body *:before,
.mce-container.mce-panel *:after,
.mce-container.mce-panel *:before,
.mce-container.mce-panel *,
.mce-container.mce-panel {
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

#eicmslinks_body #searchInput {
  width: 100%;
  display: block; }

#eicmslinks_body .table-responsive-row td:before {
  content: '' !important; }

.mce-container.mce-panel * {
  font-family: "open sans" !important; }

label[for=eicmslinks_textlink] {
  display: inline-block;
  width: auto;
  vertical-align: middle; }

#eicmslinks_textlink {
  line-height: 20px;
  width: 200px;
  height: 20px;
  padding: 0 5px;
  border: 1px solid rgba(105, 105, 105, 0.35); }

#eicmslinks_textlink_wrapper {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  border: 1px solid transparent; }

#eicmslinks_textlink_wrapper.error {
  border: 1px solid #e65858; }

#eicmslinks_textlink_wrapper span {
  display: none; }

#eicmslinks_textlink_wrapper.error #eicmslinks_textlink {
  border: 1px solid transparent;
  font-family: "open sans"; }

#eicmslinks_textlink_wrapper.error span {
  font-family: "open sans";
  font-size: 15px;
  position: absolute;
  top: 50%;
  left: 100%;
  display: block;
  width: 200px;
  padding-left: 10px;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #e65858; }

#eicmslinks_body ul.menu-link a:hover {
  text-decoration: none; }

#eicmslinks_body ul.menu-link a {
  text-decoration: none;
  color: #fff;
  font-family: sans-serif;
  display: block;
  width: 100%;
  padding: 0px 25px; }

#eicmslinks_body .link-block a:hover {
  text-decoration: none; }

#eicmslinks_body .link-block a {
  color: #207991; }

#eicmslinks_body .panel {
  border: 0 !important;
  -webkit-box-shadow: none;
          box-shadow: none; }

#eicmslinks_body {
  padding: 10px 15px 15px 15px; }

#eicmslinks_body h1 {
  margin-top: 5px;
  font: 400 14px/1.42857 Open Sans, Helvetica, Arial, sans-serif; }

.mce-close {
  display: block;
  margin: 0 250px;
  padding: 2px 10px; }

ul.menu-link {
  font-size: 0;
  width: 100%;
  height: auto;
  margin: 0px 0 18px 0;
  padding: 0;
  list-style: none;
  -webkit-border-radius: 4px;
  border-radius: 4px;
  background-color: #8bc954;
  text-align: center;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0px;
  overflow: hidden; }

ul.menu-link li a:hover {
  text-decoration: none; }

ul.menu-link li {
  font-family: "open sans";
  font-size: 12px;
  line-height: 25px;
  display: inline-block;
  width: auto;
  margin-left: 0;
  text-align: left;
  text-transform: capitalize;
  padding: 0px;
  -webkit-transition: all linear 0.25s;
  -o-transition: all linear 0.25s;
  transition: all linear 0.25s; }

ul.menu-link li:hover,
ul.menu-link li.selected {
  background-color: #76A849; }

ul.menu-link a:hover {
  text-decoration: none; }

.link-block {
  display: none; }

.mce-window-head {
  height: 15px; }

.table thead > tr.filter > th {
  padding: 4px;
  background-color: #ecf6fb; }

.table tbody > tr > td {
  font-size: 12px;
  padding: 3px 7px;
  vertical-align: middle;
  color: #666;
  border-top: medium none;
  border-bottom: 1px solid #eaedef; }

.table tbody > tr > td:hover {
  cursor: pointer; }

.icon-caret-down:before,
.icon-caret-up:before {
  content: '' !important; }

.title_box a {
  display: none; }

.panel-heading {
  font-family: sans-serif;
  font-size: 14px;
  font-weight: 400;
  height: 32px;
  white-space: nowrap;
  color: #555;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis; }

.panel {
  position: relative;
  margin-bottom: 20px;
  padding: 20px;
  border: 1px solid #e6e6e6;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #fff;
  -webkit-box-shadow: 0 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 3px #fff inset;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.1), 0 0 0 3px #fff inset; }

/* Prestashop 1.5 */
.table_grid img {
